import Vector from "./utils/vector";

export default class Spring {
  constructor(cx, cy, px, py) {
    this.center = new Vector({ x: cx, y: cy });
    this.position = new Vector({ x: px, y: py });

    this.offset = Math.floor(Math.random() * 10);

    const angle = this.center.getAngleOf(this.position);
    this.velocity = new Vector().setAngle(angle).setMag(1);

    const parallelDistance = 25;
    this.parallel = [
      this.position.calculatePointFromAngle(angle, parallelDistance),
      this.position.calculatePointFromAngle(angle + Math.PI, parallelDistance),
    ];
  }

  update(frame, width, height) {
    let shouldChange = false;

    if (Math.random() > 0.95) {
      shouldChange = true;
    }

    if (this.position.getDistance(this.center) > width * 0.45 ||
      this.position.getDistance(this.center) < width * 0.05) {
      shouldChange = true;
    }

    if (shouldChange) {
      this.velocity.rotate(Math.PI);
    }

    if (Math.random() > 0.75) {
      const speed = Math.random();
      this.velocity.setMag(speed);
    }

    this.position.add(this.velocity);
    this.parallel[0].add(this.velocity);
    this.parallel[1].add(this.velocity);
  }

  render() {
    ctx.save();
    ctx.fillRect(this.position.x, this.position.y, 4, 4);
    ctx.fillStyle = 'red';
    ctx.fillRect(this.parallel[0].x, this.parallel[0].y, 4, 4);
    ctx.fillRect(this.parallel[1].x, this.parallel[1].y, 4, 4);
    ctx.restore();
  }
}
