import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import Spring from './Spring';
import Vector from './utils/vector';
import { circlePath } from './utils/paths';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const amt = 10;

const cx = WIDTH / 2;
const cy = HEIGHT / 2;

const circle = circlePath(WIDTH / 2, HEIGHT / 2, WIDTH / 6, amt)
  .map(p => new Spring(cx, cy, p.x, p.y));

let circles = [];

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  let path = new Path2D();

  for (let i = 0; i < circle.length; i ++) {
    const { center, position, parallel } = circle[i];
    const { position: nextPosition, parallel: nextParallel } = i == circle.length - 1 ? circle[0] : circle[i + 1];

    circle[i].update(frame, width, height);

    if (i == 0) {
      path.moveTo(position.x, position.y);
    }
    path.bezierCurveTo(parallel[1].x, parallel[1].y, nextParallel[0].x, nextParallel[0].y, nextPosition.x, nextPosition.y);
  }

  circles.unshift(path);

  if (circles.length > amt * 10) {
    circles = circles.slice(0, amt * 10);
  }

  for (let i = circles.length - 1; i >= 0; i--) {
    ctx.save();
    ctx.translate(0, i * 4);
    ctx.strokeStyle = 'red';
    ctx.fillStyle = 'white';
    ctx.stroke(circles[i]);
    ctx.fill(circles[i]);
    ctx.restore();
  }
});

app.start();
